body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: black !important;
}

@font-face {
  font-family: "Broadway";
  src: local("Broadway"),
    url(/Wurtz/static/media/BroadwayFlat3DFilled-Italic.26c5a627.ttf) format("");
}
@font-face {
  font-family: "BroadwayFlat";
  src: local("BroadwayFlat"),
    url(/Wurtz/static/media/BroadwayFlat-Italic.0f59a318.ttf) format("");
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.socials {
  text-align: center;
  margin-top: 18%;
}

.socials > * {
  margin: 0% 8% 0% 8%;
  color: white;
}

.socials > *:hover {
  color: rgb(151, 197, 170);
}

#video-bg {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
  z-index: -99;
}

#video-bg > iframe {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

#video-bg .overlay {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  z-index: 2;
  background: grey;
  opacity: 0.45;
}

.gridRow {
  display: grid;
  grid-template-columns: [one] 30px [two] 40px [three] 850px [four] 60px [five] 30px [end];
}

.chevronRight {
  grid-column-start: five;
  grid-column-end: end;
}

.navigation > * > a {
  font-size: 25px;
  color: white !important;
  padding-left: 100px !important;
  padding-right: 100px !important;
}

.menu-item {
  transition: 0.3s ease-in-out;
  text-decoration: none;
  border-top: 4px solid transparent;
  border-bottom: 4px solid transparent;
  padding: 10px 0;
  margin: 0 10px;
}

.navigation {
  margin: 0 auto;
  padding-top: 10px;
}

.menu-item:hover {
  border-top: 4px solid #ffffff;
  border-bottom: 4px solid #ffffff;
  padding: 6px 0;
}

