.rotate {
  transition: all 0.5s linear;
}
.rotate-down {
  transform: rotate(90deg);
}
.rotate-up {
  transform: none;
}

.vidGear {
  -webkit-column-count: 2;
          column-count: 2;
}

.fa-chevron-right {
  margin-top: 10px;
}

.carousel-inner {
  background: white;
  margin: 5px 5px 5px 5px;
  padding: 20px 25px 25px 22px;
  width: 100% !important;
  text-align: center;
  text-decoration: none;
  box-shadow: 0 6px 8px rgba(0, 0, 0, 0.7);
  transition: all 0.15s linear;
  z-index: 0;
  position: relative;
}
.d-block {
  height: 550px;
  width: 100%;
  object-fit: cover;
  object-position: 50% 50%;
}

.carousel-indicators .active {
  background-color: white;
}
.grid {
  display: grid;
  grid-template-columns: [one] 240px [two] 40px [three] 880px [four] 40px [five] 30px [end];
}

.chevronRight {
  grid-column-start: five;
  grid-column-end: end;
}
.chevronLeft {
  grid-column-start: 2;
  grid-column-end: 2;
}

#musicSocials > * {
  margin-left: 271px;
  text-align: center;
  padding: 0px 10px 10px 10px;
  font-size: 90px;
  color: rgb(58, 121, 146);
}

.musicRow {
  display: flex;
}

#divider {
  position: flex;
  margin-left: 120px;
  margin-right: 50px;
  top: 10%;
  bottom: 15%;
  border-left: 3px solid white;
}

.record {
  background: linear-gradient(
        30deg,
        transparent 40%,
        rgba(42, 41, 40, 0.85) 40%
      )
      no-repeat 100% 0,
    linear-gradient(60deg, rgba(42, 41, 40, 0.85) 60%, transparent 60%)
      no-repeat 0 100%,
    repeating-radial-gradient(#2a2928, #2a2928 4px, #ada9a0 5px, #2a2928 6px);
}
.record:after {
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -35px;
  border: solid 1px #0573e9;
  width: 68px;
  height: 68px;
  border-radius: 50%;
  box-shadow: 0 0 0 4px #0056b3, inset 0 0 0 27px #0056b3;
  background: #b5ac9a;
  content: "";
}

.albumCover:hover + .record {
  margin-left: 415px !important;
  transition-timing-function: ease-in;
  transition-duration: 2s;
}

.albumCover {
  z-index: 10;
}

